#event-overlay {
    position: absolute;
    left: calc((40px - 2rem) / 2);
    bottom: 20px;
    width: 340px;
    padding: 1rem;
    z-index: 10;

    background: var(--color-theme-5);
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

#event-overlay.hidden {
    -webkit-transform: translateY(100vh);
        -ms-transform: translateY(100vh);
            transform: translateY(100vh);
}

.effect-slide-up-enter {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.effect-slide-up-enter-active {
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.effect-slide-up-exit {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.effect-slide-up-exit-active {
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -webkit-transform: translateY(100vh);
        -ms-transform: translateY(100vh);
            transform: translateY(100vh);
}

@-webkit-keyframes highlight-event {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        background: #FFF;
    }

    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
        background: var(--color-theme-3);
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        background: #FFF;
    }
}

@keyframes highlight-event {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        background: #FFF;
    }

    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
        background: var(--color-theme-3);
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        background: #FFF;
    }
}



