#streets {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    /* Needed to allow this to shrink (i.e. for the event log not to push
     * action bar down). This is default in Chrome, contrary to
     * standards, but not other browsers. */
    min-height: 0;
}

#streets > .labeled-panel {
    -ms-flex: 1 1;
        flex: 1 1;
}

#actions {
    -ms-flex: 1 0;
        flex: 1 0;
}

#event-log {
    -ms-flex: 0.5 1 !important;
        flex: 0.5 1 !important;
}



