.action-button {
    position: relative;
}

.action-button .action-button-annotations {
    display: -ms-inline-flexbox;
    display: inline-flex;
    position: absolute;
    right: 0.25em;
}

.action-button-lock {
    font-size: 1.5em;
    margin-top: -0.125em;
    position: absolute;
    left: 0.25em;
}

.action-button-error {
    color: var(--color-theme-alert);
    position: absolute;
    left: 0.25em;
}

.action-button-disabled {
    cursor: not-allowed;
    background: var(--color-theme-3);
}

.action-button-progress {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;

    background: var(--color-theme-1);
    -webkit-transition: width 0.5s linear;
    -o-transition: width 0.5s linear;
    transition: width 0.5s linear;
}

.action-button-progress-countdown {
    width: 0;
}

.action-button-flashing {
    -webkit-animation: 0.5s 1 flash-animation;
            animation: 0.5s 1 flash-animation;
}

@-webkit-keyframes flash-animation {
    from {
        background: var(--color-theme-3);
    }

    to {
        background: rgba(0,0,0,0);
    }
}

@keyframes flash-animation {
    from {
        background: var(--color-theme-3);
    }

    to {
        background: rgba(0,0,0,0);
    }
}



