#quest-log {
    overflow-y: scroll;
    position: relative; /* Needed to make section header scroll */
}

#quest-log > h2 {
    color: var(--color-theme-2);
    text-align: center;
    margin: 1.5em;
}

#quest-log > section {
    margin-top: 1.5em;
}

.checklist {
    list-style-type: none;
    padding: 0;
}



